let emojilist = []
let os = 0;
let arr = []
for (let key in emojiList.emoji) {
    if (emojiList.emoji[key]) {
        if (os <= 38) {
            os += 1
            arr.push({
                imageurl: emojiList.emoji[key].img,
                text: key
            })
        } else {
            emojilist.push(arr)
            os = 0
            arr = []
        }
    }
}
