import {
    convertRawMessageListToRenderChatList,
    calcTimeHeader
} from './index'
let data = {};
let nim;
let self = {};
let instantmessaging = wx.instantmessaging;
let userinfoonloginportschange = []
/**
 * this
 * @param {Object} _this 
 */
export default {
    socket: function(_this, token, account) {
        self = _this
        // console.log = function (){}
        nim = wx.NIM.getInstance({
            // debug: true,
            appKey: instantmessaging[instantmessaging.thishe].appkey,
            account: account,
            token: token,
            transports: ['websocket'],
            onloginportschange: res => {
                console.log('', res)
                // userinfoonloginportschange = res
                // if (nim) {
                //     userinfoonloginportschangefun()
                // }
            },
            onconnect: res => {
                console.log('', res)
            },
            onerror: error => {
                console.log(', ', error)
                if (nim.disconnect && nim.connect) {
                    nim.disconnect()
                    nim.connect()
                }
            },
            onofflinesysmsgs: msg => {
                console.log('', msg)
            },
            ondisconnect: error => {
                console.log('', error)
                //  `SDK` , , 
                console.log('', error);
                if (error) {
                    switch (error.code) {
                        // , 
                        case 302:
                            console.log('')
                            wx.showModal({
                                title: '',
                                content: '',
                                showCancel: false,
                                success(res) {
                                    if (res.confirm) {
                                        console.log('')
                                        wx.reLaunch({
                                            url: '/pages/index/index'
                                        })
                                    } else if (res.cancel) {
                                        console.log('')
                                        wx.reLaunch({
                                            url: '/pages/index/index'
                                        })
                                    }
                                }
                            })
                            break;
                            // , , 
                        case 417:
                            console.log(',')
                            wx.showModal({
                                title: '',
                                content: '',
                                showCancel: false,
                                success(res) {
                                    if (res.confirm) {
                                        console.log('')
                                        wx.reLaunch({
                                            url: '/pages/index/index'
                                        })
                                    } else if (res.cancel) {
                                        console.log('')
                                        wx.reLaunch({
                                            url: '/pages/index/index'
                                        })
                                    }
                                }
                            })
                            break;
                            // , 
                        case 'kicked':
                            wx.showModal({
                                title: '',
                                content: '',
                                showCancel: false,
                                success(res) {
                                    if (res.confirm) {
                                        console.log('')
                                        wx.reLaunch({
                                            url: '/pages/index/index'
                                        })
                                    } else if (res.cancel) {
                                        console.log('')
                                        wx.reLaunch({
                                            url: '/pages/index/index'
                                        })
                                    }
                                }
                            })
                            console.log(', ')
                            break;
                        default:
                            wx.showModal({
                                title: '',
                                content: '',
                                showCancel: false,
                                success(res) {
									// getCurrentPages()[getCurrentPages().length - 1].onLoad()
                                    // if (res.confirm) {
                                    //     console.log('')
                                    //     wx.reLaunch({
                                    //         url: '/pages/index/begin'
                                    //     })
                                    // } else if (res.cancel) {
                                    //     console.log('')
                                    //     wx.reLaunch({
                                    //         url: '/pages/index/begin'
                                    //     })
                                    // }
                                }
                            })
                            break;
                    }
                }
            },
            onmyinfo: userInfo => {
                self.$store.dispatch('userInfo', userInfo)
            },
            onfriends: friends => { // 
                console.log('', friends)
                data.friends = nim.mergeFriends(data.friends, friends);
                data.friends = nim.cutFriends(data.friends, friends.invalid);
                self.$store.dispatch('friendCard', data.friends)
            },
            onusers: res => {
                // console.log('', res)
            },
            onteams: teams => { // 
                // console.log('', teams);
                data.teams = nim.mergeTeams(data.teams, teams);
                data.teams = nim.cutTeams(data.teams, teams);
                data.invalidTeams = nim.mergeTeams(data.invalidTeams, teams);
                self.$store.dispatch('groupList', data.teams)
            },
            onsessions: sessions => { // 
                // console.log('', sessions)
                // 
                data.sessions = nim.mergeSessions(data.sessions, sessions)
                // 
                data.sessions.map(item => {
                    if (item.unread) {
                        self.$store.state.unreadInfo[item.id] = item.unread
                    }
                })
                data.unreadInfo = self.$store.state.unreadInfo
                self.$store.dispatch('unreadInfo', data.unreadInfo)
                // UI  1  2 3 4
                let list = convertRawMessageListToRenderChatList(data.sessions, data.friends, data.teams, data.unreadInfo)
                self.$store.dispatch('chatList', list)
            },
            onupdatesession: sessions => { // 
                // console.log('', sessions)
                // 
                data.sessions = nim.mergeSessions(data.sessions, sessions)
                self.$store.state.unreadInfo[sessions.id] = sessions.unread
                data.unreadInfo = self.$store.state.unreadInfo
                self.$store.dispatch('unreadInfo', data.unreadInfo)
                // UI  1  2 3 4
                let list = convertRawMessageListToRenderChatList(data.sessions, data.friends, data.teams, data.unreadInfo)
                self.$store.dispatch('chatList', list)
            },
            onmsg: onMsg, // 
            onroamingmsgs: onRoamingMsgs, // 
            onofflinemsgs: onOfflineMsgs, // 
            onsysmsgunread: res => {
                // console.log('', res)
            },
            onupdatesysmsgunread: res => {
                // console.log('', res)
            },
            syncRelations: true, // 
            onblacklist: res => {
                console.log('', res)
                let blacklist = {}
                for (let key in res) {
                    if (key != 'invalid') {
                        blacklist[res[key].account] = res[key]
                    }
                }
                wx.USER.blacklist = blacklist
            },
            onsyncmarkinblacklist: res => {
                console.log('', res)
            },
            onmutelist: res => {
                console.log('', res)
            },
            onsyncmarkinmutelist: res => {
                console.log('', res)
            },
            syncSessionUnread: true,
            syncRoamingMsgs: true,
            db: false,
            promise: true,
            onsyncdone: res => {
                console.log('', res)
            }
        });
        // 
        // userinfoonloginportschangefun()

        function userinfoonloginportschangefun() {
            let kick = []
            for (let i = 0, len = userinfoonloginportschange.length; i < len; i++) {
                kick.push(userinfoonloginportschange[i].deviceId)
            }
            if (kick.length <= 0) {
                return
            }
            nim.kick({
                deviceIds: kick,
                done: (error, obj) => {
                    console.log('' + (!error ? '' : ''));
                }
            });
        }
        return nim
    }
}


function onRoamingMsgs(obj) {
    // console.log('', obj);
    pushMsg(obj.msgs);
}

function onOfflineMsgs(obj) {
    // console.log('', obj);
    pushMsg(obj.msgs);
}

function onMsg(msg) {
    // console.log('', msg.scene, msg.type, msg);
    // let list = self.$store.state.chatList
    // let is = '-1'
    // for (let i = 0; i < list.length; i++) {
    //     if (list[i].account == msg.from) {
    //         is = '1'
    //         list[i].chatType = msg.scene
    //         list[i].unread += 1
    //         list[i].type = msg.type
    //         list[i].lastestMsg = msg.text
    //         list[i].timestamp = msg.time
    //         list[i].displayTime = calcTimeHeader(msg.time)
    //         break;
    //     } else {
    //         is = '-1'
    //     }
    // }
    // list.sort((a, b) => {
    //     return b.timestamp - a.timestamp
    // })
    // data.sessions = list
    let list = convertRawMessageListToRenderChatList(data.sessions, data.friends, data.teams, data.unreadInfo)
    self.$store.dispatch('chatList', list)
    pushMsg(msg);
}

function pushMsg(msgs) {
    // 
    data.msgs = self.$store.state.thischatList || {}
    if (!Array.isArray(msgs)) {
        msgs = [msgs];
    }
    var sessionId = msgs[0].sessionId;
    data.msgs = data.msgs || {};
    data.msgs[sessionId] = nim.mergeMsgs(data.msgs[sessionId], msgs);
    self.$store.dispatch('thischatList', data.msgs)
}